.vh {
    height: 100vh;
    width: 100%;
}
.section {
    padding: 25px;
    border-radius: 16px;
    box-shadow: 0px 7px 30px 2px rgba(0,0,0,0.18);
    /* margin-bottom: 45px; */
    margin: 5px 45px;
    background: white;
}

.scrollable-content-header {
    padding: 25px 0;
    font-weight: bold;
    color: white;
}
.scrollable-content {
    height: 100vh;
    overflow-y: scroll;
    background: linear-gradient(to right, #2e51a2, #1b7ac2);
    /* background: #f9f9f9; */
}

.no-select {
    user-select: none;          /* supported by Chrome and Opera */
    -webkit-user-select: none;  /* Safari */
    -moz-user-select: none;     /* Firefox */
    -ms-user-select: none;      /* Internet Explorer/Edge */
}

.text-disable { color: #C6C6C6; }
.text-bold { font-weight: 700; } /* Bold from google fonts */
