/* Override */
.section {
    padding: 0;
    margin: 25px 20px;
    border-radius: 16px;
}

.project-preview {
    padding: 60px;
    border-top-left-radius: 14px;
    border-top-right-radius: 14px;
}

.project-image {
    border-radius: 4px;
    width: 95%;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.3);

    /* To center image */
    display: block;
    margin-left: auto;
    margin-right: auto;

    transition: transform .2s;
}

.project-image:hover {
    transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

.project-title {
    font-size: 20px;
    font-weight: bold;
    color: black;

    /* color transform */
    -webkit-transition: color 0.25s;
    -moz-transition:    color 0.25s;
    -ms-transition:     color 0.25s;
    -o-transition:      color 0.25s;
    transition:         color 0.25s;
}

.project-title:hover {
    text-decoration: none;
    color: #2e51a2;
}

.project-repo { font-size: 36px; }
.project-repo:hover { cursor: pointer; color: #2e51a2; }

.project-description {
    color: #555555;
    font-size: 13px;
}

.project-meta-data {
    font-size: 14px;
    margin: 0;
}

.meta-icon { padding-right: 4px; }
.project-body {
    padding: 25px;
    padding-bottom: 30px;
}


/* Fade In */

.fade-in-section {
    opacity: 0;
    transform: translateY(20vh);
    visibility: hidden;
    transition: opacity 1200ms ease-out, transform 600ms ease-out,
        visibility 1200ms ease-out;
    will-change: opacity, transform, visibility;
}
.fade-in-section.is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
}


/* Tags */

.tags-wrapper { display: flex; }
@media only screen and (max-width: 600px) {
    .tags-wrapper { display: block; }       /* Overflow problem fixed on mobile (iphone 6) */
}
