.toooltip {
    position: relative;
    display: inline-block;
    color: black;
    cursor: pointer;
}

.tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #e9e9e9;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;

    bottom: 100%; left: 0;

    position: absolute;
    z-index: 1;

    transition: opacity 1s;
}

.toooltip:hover { color: #2e51a2; }

.toooltip:hover .tooltiptext {
    color: black;
    visibility: visible;
    opacity: 1;
}
