.navbar {
    background: linear-gradient(to right, #2e51a2, #1b7ac2);
    position: fixed;
    width: 100%;
    box-shadow: 0 2px 4px -1px rgba(0,0,0,0.25);
    visibility: hidden;
    z-index: 100;
    color: white;
}

.nav-item {
    font-size: 24px;
    background-color: transparent !important;
    padding: 0px 10px;
}

@media (max-width: 768px) {
    .navbar { visibility: visible; }       /* Overflow problem fixed on mobile (iphone 6) */
}
