.main { height: 100vh; }
.left { height: 100vh; }

.profile-name {
    font-size: 30px;
    font-weight: bold;

    margin: 50px 0;

    overflow: hidden;
    text-align: center;
}
.profile-name:before,
.profile-name:after {
    content: "";
    display: inline-block;

    border-top: 1px white black;
    border-bottom: 1px white black;
    background-color: black;

    height: 4px;
    width: 50%;

    position: relative;
    vertical-align: middle;
}
.profile-name:before { right: 0.5em; margin-left: -50%; }
.profile-name:after { left: 0.5em; margin-right: -50%; }

.profile-picture {
    width: 50%;
    /* background-color: red; */
    border-radius: 500px;
    margin-bottom: 25px;
}

/* Social Btns */
.btn-hoverable:hover {
    color: #2e51a2;
}

.btn-social {
    font-size: 36px;
    transition: transform .2s;
}
.btn-social:hover {
    transform: scale(1.1);
}
.btn-social:focus { outline:0; }
.btn-resume {
    border: 4px solid transparent;
}
.btn-resume:hover {
    border: 4px solid #2e51a2;
}

/* */
.list-group-item { border-bottom: 1px solid #e9e9e9; }
.btn-nav {
    font-weight: bold;
    font-size: 20px;
    padding: 20px 0;
}
.btn-nav-hoverable:hover {
    background-color: #e9e9e9;
    color: #2e51a2;
    cursor: pointer;
}
.btn-nav-active {
    background-color: #2e51a2;
    color: white;
}

@media (max-width: 768px) {
    .header-nav { display: none; }       /* Overflow problem fixed on mobile (iphone 6) */
    /* .header-nav { display: none; } */
    .left { display: none; }
    .scrollable-content-header {
        margin-top: 1.5em;
    }
}


.center-wrapper { display: flex; margin: auto; }
.center-content { margin: auto; text-align: center; }
