.bar {
    padding-left: 15px;
    /* background-color: #2e51a2; */
    background: linear-gradient(to right, #2e51a2, #1b7ac2);
    border-radius: inherit;
}

.section-about {
    padding: 25px;
}

.skill-bar {
    border: 1px #2e51a2 solid;
    border-radius: 25px;
    color: white;
    font-weight: bold;
    margin: 10px 0;
}

.slide-in-bar {
    width: 0%;
    border-radius: inherit;
    transition: width 1s ease-out;
    will-change: width;
}
.slide-in-bar.side-animate {
    transform: none;
    width: 100%;
}
